:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: .25s;
}

body {
  background-color: #e3cff6;
}

header {
  background-color: #bcf8fa;
  justify-content: center;
  height: 50px;
  margin-bottom: 10px;
  display: flex;
}

img {
  transition: transform var(--animation-duration) var(--timing-function);
  border-radius: 3px;
  height: 175px;
}

img:hover {
  transform: scale(1.03);
}

p {
  color: #4e4c4c;
  font-style: oblique;
  font-weight: 500;
  font-stretch: condensed;
}

a {
  text-decoration: none;
}

.search-form {
  justify-content: center;
  align-items: center;
  gap: 10px;
  display: flex;
}

.input-style {
  background-color: #faebd7;
  border: 2px solid #7171d2;
  border-radius: 10px;
  padding: 5px 10px;
}

.button-search {
  cursor: pointer;
  background-color: #faebd7;
  border: 2px solid #7171d2;
  border-radius: 10px;
  padding: 5px 10px;
}

.button-search:hover, .button-search:focus, .button-search:active {
  background-color: bisque;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 20px;
  display: flex;
}

.photo-card {
  cursor: zoom-in;
  background-color: #bcf8fa;
  border: 5px solid #bcf8fa;
  border-radius: 3px;
  box-shadow: 0 1px 3px #0003, 0 1px 1px #00000024, 0 2px 1px -1px #0000001f;
}

.info {
  padding: 0 5px;
}

.is-hidden {
  display: none;
}

.load-more {
  cursor: pointer;
  background-color: #bcf8fa;
  border: none;
  border-radius: 10px;
  margin: 20px auto;
  padding: 10px 20px;
  font-style: oblique;
  font-weight: 500;
  font-stretch: condensed;
  display: none;
}

.load-more:hover {
  background-color: #9ef3d6;
}

.warning-hint {
  text-align: center;
}

